import { Component, OnInit } from '@angular/core';
import {UsuarioService} from '../../Services/usuario.service';
import {Usuario} from '../../Models/usuario';
import {FormGroup,FormBuilder} from '@angular/forms'
import { CurrencyPipe } from '@angular/common';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit{

  userSelected: Usuario;
  userForm: Usuario;
  users: Usuario[];
  mostrar: boolean;

  constructor(public usuarioService: UsuarioService,private formBuilder:FormBuilder) {

    // this.userForm.Name='';
    // this.userForm.Description='';
    // this.userForm.Salario=CurrencyPipe.arguments(2);
  }
  // {
  //   Nombre:[''],
  //   Description:[''],
  //   Salario:['']
  // }


  formularioRegistro=this.formBuilder.group(
    this.userForm= new Usuario(0,'','',0)
  )

  ngOnInit(){

    this.TUsuario();
  }
  enviar(){
    console.log(this.formularioRegistro.value);
    this.CUsuario(this.formularioRegistro.value);
  }

  CUsuario(usuario:Usuario){
    try
    {
      this.usuarioService.CrearUsuario(usuario).subscribe(result=>{
        this.userSelected=result;
        if(this.userSelected.RespuestaOperacion.Exito){
          this.TUsuario();
        }
      });
    }
    catch
    {
      console.error('algo salio mal =(');
    }

  }

  TUsuario() {
    this.usuarioService.TraerUsuario()
      .subscribe(result => {
        this.users = result;
        console.log(this.users);
    }, error => console.error(error));
  }
}
